import React, { Component } from 'react';
import { NavMenu } from './NavMenu'
import { Container, Row, Col } from 'reactstrap';
import './NavMenu.css';
import { Dashboard } from '../menu/Dashboard'
import { Inbox } from '../menu/Inbox'
import { Outbox } from '../menu/Outbox'
import { TimeSheets } from '../menu/TimeSheets'
import { Manage } from '../menu/Manage'
import { Settings } from '../menu/Settings'
import { Help } from '../menu/Help'
import { Test } from '../menu/Test'
import { Billing } from '../menu/Billing'
import { Shifts } from '../menu/Shifts'
import { Employee } from '../menu/Employee'

export class Home extends Component {
    static displayName = Home.name;
    constructor(props) {
        super(props);

        this.state = {
            key: <Dashboard />,
            components: [
                { id: 1, componentClass: "icofont-dashboard icofont-3x", name: "dashboard", compoName: "Dashboard" },
                { id: 2, componentClass: "icofont-inbox icofont-3x", name: "inbox", compoName: "Inbox" },
                { id: 3, componentClass: "icofont-upload-alt icofont-3x", name: "outbox", compoName: "Outbox" },
                { id: 4, componentClass: "icofont-file-spreadsheet icofont-3x", name: "timesheets", compoName: "Timesheets" },
                { id: 5, componentClass: "icofont-calendar icofont-3x", name: "shifts", compoName: "Shifts" },
                { id: 6, componentClass: "icofont-document-folder icofont-3x", name: "employee self service", compoName: "Employee Self Service" },
                { id: 7, componentClass: "icofont-binary icofont-3x", name: "manage", compoName: "Manage" },
                { id: 8, componentClass: "icofont-credit-card icofont-3x", name: "billing", compoName: "Billing" },
                { id: 9, componentClass: "icofont-gear icofont-3x", name: "settings", compoName: "Settings" },

                //{ id:9, componentClass: "icofont-question-circle icofont-3x", name: "help", compoName: "Help" },
            ],
            activeID: 1,
            isActive: true,
            isHide: false,
            isExpand: false,
            componentTitle: 'Dashboard'
        };
        this.getData = this.getData.bind(this);
    }
    handleClick = (e) => {
        if (window.innerWidth < 481) {
            this.setState({
                isHide: !this.state.isHide

            })
        }
        console.log(e.target.getAttribute('name'))
        if (e.target.getAttribute('name') === 'dashboard') {
            this.setState({ key: <Dashboard />, componentTitle: 'Dashboard'  })
        } else if (e.target.getAttribute('name') === 'inbox') {
            this.setState({ key: <Inbox />, componentTitle: 'Inbox' })
        } else if (e.target.getAttribute('name') === 'outbox') {
            this.setState({ key: <Outbox />, componentTitle: 'Outbox' })
        } else if (e.target.getAttribute('name') === 'timesheets') {
            this.setState({ key: <TimeSheets />, componentTitle: 'Timesheets' })
        } else if (e.target.getAttribute('name') === 'manage') {
            this.setState({ key: <Manage />, componentTitle: 'Manage' })
        } else if (e.target.getAttribute('name') === 'settings') {
            this.setState({ key: <Settings />, componentTitle: 'Settings' })
        } else if (e.target.getAttribute('name') === 'billing') {
            this.setState({ key: <Billing />, componentTitle: 'Billing' })
        } else if (e.target.getAttribute('name') === 'help') {
            this.setState({ key: <Help />, componentTitle: 'Help' })
        }
        else if (e.target.getAttribute('name') === 'test') {
            this.setState({ key: <Test />, componentTitle: 'Test' })
        }
        else if (e.target.getAttribute('name') === 'employee') {
            this.setState({ key: <Employee />, componentTitle: 'Employee Self Service' })
        }
        else if (e.target.getAttribute('name') === 'shifts') {
            this.setState({ key: <Shifts />, componentTitle: 'Shifts' })
        }
        const id = parseInt(e.target.id);
        this.setState({
            activeID: id
        });
        console.log(this.state.activeID)
    }
    getData(val, title) {
        this.setState({
            key: val,
            componentTitle: title
        });
    }
    backButton() {
        this.setState({
            isHide: !this.state.isHide
        })
    }
    clickExpand() {
        if (window.innerWidth > 768) {
            this.setState({
                isExpand: !this.state.isExpand
            })
        }
    }
    render() {
       
        const { key } = this.state;
        return (
            <React.Fragment>
                {!this.state.isHide && <NavMenu sendData={this.getData} /> }
                <div className="mobileOnly">
                    {!this.state.isHide &&
                        <i
                            onClick={() =>
                                this.setState({
                                    isActive: !this.state.isActive
                                })
                            }
                            className={
                                this.state.isActive
                                    ? "icofont-navigation-menu"
                                    : "icofont-ui-close"
                            }
                            style={{
                                color: "#fff",
                                fontSize: "20px",
                                position: "absolute",
                                left: "5%",
                                top: "5%",
                                cursor:"pointer",
                                zIndex: "9"
                            }}
                        ></i>
                    }
                        </div>
                {!this.state.isHide &&<ul
                    className={
                        this.state.isActive && !this.state.isExpand
                            ? "sideNav active"
                            : "sideNav"
                    }
                >
                    {this.state.components.map((c, i) => {
                        return <MyComponent active={c.id === this.state.activeID} id={c.id} onClick={this.handleClick.bind(this)} componentClass={c.componentClass} name={c.name} compoName={c.compoName} key={i} />
                    })}
                    <div
                        onClick={this.clickExpand.bind(this)}
                        className={
                            this.state.isExpand
                        ? "expand-area icofont-thin-double-right"
                        : "expand-area icofont-thin-double-left"
                        }
                        ></div>
                </ul>}
                <div
                    className={
                        this.state.isExpand
                            ? "custom-container isExpand"
                            : "custom-container isNotExpand"
                    }
                >
                    <div className="inner-custom-container">
                        <div className="header-title h3">
                            {this.state.isHide && <i className="icofont-rounded-left icofont-2x backBtn" onClick={this.backButton.bind(this)} style={{ cursor: "pointer" }}></i>}
                            {this.state.componentTitle}
                        </div>
                        {key}
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

class MyComponent extends React.Component {
    render() {
        return <li className={"item" + (this.props.active ? ' active ' : '')} id={this.props.id} onClick={this.props.onClick}>
            <i className={this.props.componentClass} name={this.props.name} id={this.props.id} style={{ color: '#2f9c91' }}> </i>  <span name={this.props.name} id={this.props.id}>{this.props.compoName}</span>
        </li>
    }
}



