import React, { Component } from 'react';
import {  Navbar, NavbarBrand} from 'reactstrap';
import './NavMenu.css';
import { Help } from '../menu/Help';
import { Test } from '../menu/Test';
export class NavMenu extends Component {
  static displayName = NavMenu.name;

  constructor (props) {
    super(props);

    this.toggleNavbar = this.toggleNavbar.bind(this);
    this.state = {
      collapsed: true
      };
      this.getHelp = this.getHelp.bind(this);
      this.getTest = this.getTest.bind(this);
  }

  toggleNavbar () {
    this.setState({
      collapsed: !this.state.collapsed
    });
  }
    getHelp  () {
        this.props.sendData(<Help/>, 'Help');
    }
    getTest() {
        this.props.sendData(<Test />, 'Test');
    }
  render () {
      return (
          <header>
              <Navbar className="myNav navbar-expand-sm navbar-toggleable-sm ng-white">
                  <NavbarBrand className="colLeft"><li>MyCompany</li>
                      <li className="list-item" style={{ cursor: "pointer" }} onClick={this.getTest}>Test</li>
                  </NavbarBrand> 
                  <ul className="colRight">
                      <li className="list-item " onClick={this.getHelp}><i className="icofont-question-circle icofont-3x" style={{cursor: "pointer"}}></i></li>
                      <li className="list">Clarice Morcilla<a href="/">claricegwapa@gmail.com</a></li>
                  </ul>
              </Navbar>
          </header>
    );
  }
}



