import React, { Component } from 'react';
import './menu-sass/shift.min.css';
import Calendar from '../calendar/Calendar';
import leftPad from 'left-pad';
import { WorkSchedule } from '../components/WorkSchedule'
export class Shifts extends Component {

    constructor(props) {
        super(props)

        const today = new Date();

        const dateOfMonth = today.getDate();
        const monthOfYear = today.getMonth()+1 ; // 0 based
        const year = today.getFullYear();
        this.state = {
            day: dateOfMonth,
            month: monthOfYear,
            year: year,
            arrowFirstUp: true,
            isHidden: false,
            heightFull: true,
            arrowSecondUp: true,
            isHidden2: false,
            months: [
                {month: 'January' },
                {month: 'February' },
                {month: 'March' },
                {month: 'April' },
                {month: 'May' },
                {month: 'June' },
                {month: 'July' },
                {month: 'August' },
                {month: 'September' },
                {month: 'October' },
                {month: 'November' },
                {month: 'December' },
            ],
        }
    
    }
    


   

    render() {
        const { isHidden, isHidden2 } = this.state
        const end = 2020;
        const start = 1900;
        const yearRange = Array.from({ length: (end - start) }, (v, k) => k + start);

        const formattedDate = [
            leftPad(this.state.year, 4, 0),
            leftPad(this.state.month, 2, 0),
            leftPad(this.state.day, 2, 0)
        ].join("-");

        return (
            
            <React.Fragment>
                <div className="shiftHeader">
                    <span className="icofont-arrow-left icofont-3x" style={{ display: 'flex', alignItems:'center' }}> </span>
                    <span className="shiftHeaderText" ></span>
                </div>

                <form className="year-selection">
                    <label htmlFor="month">Month</label>
                    <select name="month" value={this.state.month} onChange={(e) => this.setState({ month: e.target.value })}>
                        <option value="01">January</option>
                        <option value="02">February</option>
                        <option value="03">March</option>
                        <option value="04">April</option>
                        <option value="05">May</option>
                        <option value="06">June</option>
                        <option value="07">July</option>
                        <option value="08">August</option>
                        <option value="09">September</option>
                        <option value="10">October</option>
        x                <option value="11">November</option>
                        <option value="12">December</option>
                    </select>

                    <label htmlFor="year">Year</label>

                    <select name="year" value={this.state.year} onChange={(e) => this.setState({ year: e.target.value })}>
                        {yearRange.map((year) => {
                            return <option key={year} value={year}>{year}</option>
                        })}
                    </select>
                </form>

                <div className="shiftSecondContainer">
                    <div className="shiftFirstColumn">
                        <ul style={{listStyle: 'none', padding: '0', margin:'0', textAlign:'center'}}>
                            <li style={{ width: '100%', background: '#2f9c91', color:'white'}}>CALENDARS</li>
                            <li style={{width:'100%', background: 'yellow' }}>Company</li>
                            
                        </ul>
                    </div>
                     <Calendar date={formattedDate} />
                 
                    <div className="shiftThirdColumn">
                        <div style={{ width: '100%', height: '20%', padding: '4%', border: '1px solid #ccc', borderBottom:'none'}}>
                            <ul className="shiftThirdColumnList">
                                <li style={{fontWeight: 'bold',  height:'40%'}}>
                                    September 17, 2019
                                   
                                </li>              
                                <li style={{ display: 'flex',  height:'25%' }}>
                                    <span style={{ width: '20%' }} className="shiftThirdColumnElements" >Day </span>
                                    <span style={{ width: '80%' }} className="shiftThirdColumnElements">Regular Work Day </span>
                                </li>
                                <li style={{ display: 'flex', height: '25%' }}>
                                    <span style={{ width: '20%' }}  className="shiftThirdColumnElements">Code </span>
                                    <span style={{ width: '80%', background: '#37ff00' }} className="shiftThirdColumnElements"> REGD </span>
                                </li>
                            </ul>
                        </div>
                        <div style={{ width: '100%', height:(!isHidden ? '30%' : false),border: '1px solid #ccc', borderBottom: 'none' }}>
                            <span style={{ height: '35px', background: '#2f9c91', color: 'white' }} className="shiftThirdColumnElements">
                                <span style={{ width: '63%', justifyContent: 'flex-end', display: 'flex' }}> Work Schedule</span>  <span id="1" onClick={() => this.setState({
                                       isHidden:!isHidden })} style={{ cursor: 'pointer', width: '37%', justifyContent: 'flex-end', display: 'flex' }} className={"icofont-arrow-" + (!isHidden ? 'up' : 'down') + " icofont-2x"} />
                            </span>
                            {!isHidden && <WorkSchedule/>}
                        </div>
                        <div style={{ width: '100%', height:(!isHidden2 ? '50%' : false), border: '1px solid #ccc'}}>
                            <span style={{ height: '35px', background: '#2f9c91', color: 'white' }} className="shiftThirdColumnElements">
                                <span style={{ width: '55%', justifyContent: 'flex-end', display: 'flex' }}> Shift</span>  <span onClick={() => this.setState({
                                        isHidden2: !isHidden2})} style={{ cursor: 'pointer', width: '45%', justifyContent: 'flex-end', display: 'flex' }} className={"icofont-arrow-" +(!isHidden2 ? 'up' : 'down')+" icofont-2x"} /> 
                            </span>
                            {!isHidden2 && <ul className="shiftThirdColumnLastList">
                                <li style={{ display: 'flex', height: '30%', border: '1px solid #ccc', marginBottom: '2%' }}>
                                    <span style={{
                                        width: '50%', background: '#a3faff'
                                    }} className="shiftThirdColumnElements"> [9-01] <br /> Day Shift </span>
                                    <ul style={{ padding: '0', width: '50%', display: 'flex', flexDirection: 'column' }} className="shiftThirdColumnElements">
                                        <li className="fixedTime" style={{ background: '#c8f7d3', color: 'black', border: 'none' }}> START - END</li>
                                        <li className="fixedTime" style={{ background: 'white', color: 'black', border: 'none' }}> 08:00 - 18:00 </li>
                                        <li className="fixedTime"> FIXED TIME</li>
                                    </ul>
                                </li>
                                <li style={{ display: 'flex', flexDirection: 'row', height: '40%', marginBottom: '4%' }}>
                                    <ul style={{ padding: '0', width: '37%', display: 'flex', flexDirection: 'column' }} className="shiftThirdColumnElements">
                                        <li className="fixedTime" style={{ background: '#06bf31', border: 'none' }}> START</li>
                                        <li className="fixedTime" style={{ background: '#cfc540', color: 'black', border: 'none' }}> BREAK START </li>
                                        <li className="fixedTime" style={{ background: '#9c7500', border: 'none' }}> BREAK END</li>
                                        <li className="fixedTime" style={{ background: 'red', border: 'none' }}> END </li>
                                    </ul>
                                    <ul style={{ padding: '0', width: '26%', display: 'flex', flexDirection: 'column' }} className="shiftThirdColumnElements">
                                        <li className="fixedTime" style={{ background: 'white', border: '#ededed', color: 'black' }}>08:00</li>
                                        <li className="fixedTime" style={{ background: 'white', border: '#ededed', color: 'black', borderTop: '#c4c4c4 1px solid' }}>12:00 </li>
                                        <li className="fixedTime" style={{ background: 'white', border: '#ededed', color: 'black', borderBottom: '#c4c4c4 1px solid' }}> 13:00</li>
                                        <li className="fixedTime" style={{ background: 'white', border: '#ededed', color: 'black' }}> 18:00</li>
                                    </ul>
                                    <ul style={{ padding: '0', width: '37%', display: 'flex', flexDirection: 'column', border: 'none' }} className="shiftThirdColumnElements">
                                        <li className="fixedTime" style={{ background: 'white', border: 'none', color: 'black', height: '25%' }} />
                                        <li className="fixedTime" style={{ background: 'white', border: '1px solid #c4c4c4', borderLeft: 'none', color: 'black', height: '50%' }}>NO PAY<br /> BREAK </li>
                                        <li className="fixedTime" style={{ background: 'white', border: 'none', color: 'black', height: '25%' }} />
                                    </ul>

                                </li>
                                <li style={{ display: 'flex', flexDirection: 'column', height: '20%', border: 'none', }}>
                                    <ul style={{ padding: '0', width: '100%', display: 'flex', flexDirection: 'row', border: 'none', marginBottom: '2%' }} className="shiftThirdColumnElements">
                                        <li className="fixedTime" style={{ height: '100%', background: 'white', color: 'black', border: 'none', width: '60%' }}> Work Hours:</li>
                                        <li className="fixedTime" style={{ height: '100%', background: '#e0e0e0', color: 'black', border: 'none', width: '40%' }}> 9.0 </li>

                                    </ul>
                                    <ul style={{ padding: '0', width: '100%', display: 'flex', flexDirection: 'row', border: 'none' }} className="shiftThirdColumnElements">
                                        <li className="fixedTime" style={{ height: '100%', background: 'white', color: 'black', border: 'none', width: '60%' }}> No Pay Break Hours:</li>
                                        <li className="fixedTime" style={{ height: '100%', background: '#e0e0e0', color: 'black', border: 'none', width: '40%' }}> 1.0 </li>
                                    </ul>
                                </li>


                            </ul>}
                        </div>
                    </div>
                  
                </div>
                <footer className="shiftFooter">
                    LEGEND
                     <ul style={{ padding: '0', width: '35%', display: 'flex', flexDirection: 'row', border: 'none', margin: '0' }} className="shiftThirdColumnElements">
                        <li className="time" style={{ height: '100%', background: '#c5ffff', color: 'black', border: '1px solid gray', width: '60%' }}> FIXED:</li>
                        <li className="time" style={{ height: '100%', background: '#c5ffff', color: 'black', border: '1px solid gray', width: '95%', borderLeft: 'none' }}> 08:00-18:00 </li>
                    </ul>
                    <ul style={{ padding: '0', width: '35%', display: 'flex', flexDirection: 'row', border: 'none', marginTop: '1%' }} className="shiftThirdColumnElements">
                        <li className="time" style={{ height: '100%', background: '#bf9000', color: 'white', border: '1px solid gray', width: '60%',  }}> FIXED:</li>
                        <li className="time" style={{ height: '100%', background: '#bf9000', color: 'white', border: '1px solid gray', width: '95%', borderLeft: 'none' }}> 08:00-17:00 </li>
                    </ul>
                </footer>
            </React.Fragment>
        );
    }
}



