import React, { Component } from 'react';
import { Row, Col, FormGroup,Label,Input } from 'reactstrap';

export class LocationSetting extends Component {


    constructor(props) {

        super(props);
        this.state = {

            geolocation: false
            
        };

    }

    handleGeoLocationChange =() => {
        this.setState({
            geolocation:!this.state.geolocation
        })

    }

    render() {
        var arrowClass = this.props.isShown ? "fas fa-arrow-up" :"fas fa-arrow-down"
        return (
            <div id="location-set-content" >
                <div id = "setting-heading" 
                    onClick={this.props.onClick} >
                    <Row>
                        <Col lg={{size:8,offset:2}}>
                            Location Setting
                        </Col>
                        <Col lg={{ size: 2}}>
                            <span className={arrowClass}
                                style={{ textAlign: 'right' }}
                                ></span>
                        </Col>
                    </Row>
                </div>
                <div>
                    {
                        this.props.isShown ? <Setting
                            onClick={this.handleGeoLocationChange.bind(this)} /> : null
                    }
                </div>
                
              
            </div>
        );
    }
}
export class Setting extends Component {

    constructor(props) {
        super(props)
        this.state = {
            geolocation: false

        };

    }

    render(){

        return (
            <div id = "setting-body">
                <Row>
                    <Col id = "setting-label" lg="6">
                        <span className="font-weight-bold">Use Geo Location</span>
                    </Col>
                    <Col id="setting-control"  lg="6">
                        <label className="switch" >
                            <input type="checkbox" className = "checked" />
                            <span className="slider round" onClick={this.props.onClick} ></span>
                        </label>
                    </Col>
                </Row>
                <Row>
                    <Col id="desc-label" lg="12" >
                        <p >If enabled, device name will be base on the current location
                            detected via GPS positioning. Otherwise Device Location (Manual) will be used.</p>
                    </Col>
                </Row>
                <Row>
                    <Col lg="9">
                    <div id = "manual-input">
                            <FormGroup>
                                <Label for="company">Device Location (Manual)</Label>
                                <Input type="text" name="lastcom" id="lastcom" />
                            </FormGroup>
                        </div>
                    </Col>
                    <Col lg="3">

                    </Col>
                </Row>
            </div >);

    }
} 


