import React, { Component } from 'react';
export class Dashboard extends Component {


    constructor(props) {
        super(props);
        this.state = {};


    }



    render() {
        return (
            <div className="dashboardContainer" >
                <span className="dashboardHeader h3"></span>
                <div className="card-group row timeManager">
                    <div className="card col-sm-6 timeKeeping">
                        <div className="card-body " style={{ boxShadow: 'rgba(0, 0, 0, 0.25) 3px 3px 14px 2px, rgb(255, 255, 255) 4px 4px 14px 1px' }}>
                            <h5 className="card-title text-center text-info">Paxypad Time Keeping Devices</h5>
                            <div className="row pt-4">
                                <ul className="col-sm-3 text-center">
                                    <li className="text-primary font-weight-bold">2 </li>
                                    <li className="mt-4">For Delivery </li>
                                </ul>
                                <ul className="col-sm-3 text-center">
                                    <li className="text-primary font-weight-bold">0 </li>
                                    <li className="mt-4">Registered </li>
                                </ul>
                                <ul className="col-sm-3 text-center">
                                    <li className="text-primary font-weight-bold">0 </li>
                                    <li className="mt-4">Up </li>
                                </ul>
                                <ul className="col-sm-3 text-center">
                                    <li className="text-primary font-weight-bold">0 </li>
                                    <li className="mt-4"> Down</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                    <div className="card col-sm-4 attendance">
                        <div className="card-body" style={{ boxShadow: 'rgba(0, 0, 0, 0.25) 3px 3px 14px 2px, rgb(255, 255, 255) 4px 4px 14px 1px' }}>
                            <h5 className="card-title text-center text-info">Attendance today 9/20/2019</h5>
                            <div className="row pt-4">
                                <ul className="col-sm-4 text-center">
                                    <li className="text-primary font-weight-bold">0 </li>
                                    <li className="mt-4">Present </li>
                                </ul>
                                <ul className="col-sm-4 text-center">
                                    <li className="text-primary font-weight-bold">0 </li>
                                    <li className="mt-4">On-Leave </li>
                                </ul>
                                <ul className="col-sm-4 text-center">
                                    <li className="text-primary font-weight-bold">0 </li>
                                    <li className="mt-4">Late </li>
                                </ul>

                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
}



