import React from 'react';


export default class Compo2 extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            timeStatus: props.timeStatus,
            status: props.status,
            time: props.time,
            border: props.border,
            day: props.day,
            key: props.key,
            input: props.input
        };
        
    }

    render() {
        const {input} = this.state
        return (
            <ul className={"status-custom " + this.state.input +'2'}>
           
                 
                    
               <li className={this.state.input} > 
                  
                    <span className="workSpan" style={{ borderRight: '1px solid black', color: this.props.color, width: '40%' }}>{this.props.text}</span>
                    <span className="workSpan" style={{ color: this.props.color, width: '60%' }}>{this.state.input}</span>
                    
                </li>
               
                <li className="fixedTime">
                    FIXED TIME
                </li>


                  
                <li className="time">
                    08:00 <br />
                    {(function () {
                        switch (input) {
                            case ('WORK'):
                                return   '18:00'
                            case ('REST'):
                                return  '17:00'
                        } 
                    
                    })()}
                </li>

     

            </ul>
        );
    }
}



