import React, { Component } from 'react';
export class BankDeposit extends Component {
    
    render() {
        
        return (
            <div>
                <div>
                    <h4 style={{ fontSize: '20px' }}> Bank Deposit Payment</h4>
                    <p>1. Deposit payment to:<br /> Account No.:556-5-66666-6<br />Account Name:Paxyroll</p>
                    <p>2. Get photo or scanned copy of deposit slip/receipt</p>
                    <p>3. Upload photo or scanned copy of deposit slip/receipt to Billing/Invoices</p>
                </div>
                <div className="font-color" style={{textAlign:'left',paddingLeft:'50px' }}>
                    <i className="fas fa-file-invoice-dollar fa-3x"
                        style={{ cursor: 'pointer' }}></i><br />Billing
                </div>
            </div>
        );
    }
}
export default BankDeposit;


