import React from 'react';
import Compo2 from './compo2'
import ReactPaginate from 'react-paginate';



export default class WeekDisplay extends React.Component {
    constructor(props) {
        super(props);
        let currentDay = this.props.day;


        this.state = {
            date: this.props.date,
            day: 'day',
            offset: 1,
            data: this.props.weeks,
            elements: null,
            perPage: 1,
            currentPage: 0,
            pageCount: Math.ceil(this.props.weeks.length / 1),
            currentDay: currentDay,
            day1: this.props.day,
            currentDay: currentDay

        };


    }
    componentDidMount() {
        if (this.props.day[0] === '0') {
            this.setState({ currentDay: this.props.day.slice(1) });
        }
        const { day1, currentDay } = this.state;
        if (this.state.elements === null) {
            this.setState({
                elements: (<ul className="week2">

                    {this.props.weeks[0].map((day, di) => {


                        return (function () {
                            switch (true) {
                                case (di === 0 && day !== ''):
                                    return <li className='day' key={di}>
                                        <div className="actualDay">
                                            {day || "\u00a0"}
                                        </div>
                                        <Compo2 input="REST" color="white" text="REST" />
                                    </li>
                                case (di === 6 && day !== ''):
                                    return <li className='day' key={di} >
                                        <div className="actualDay">
                                            {day || "\u00a0"}
                                        </div>
                                        <Compo2 input="REST" color="white" text="REST" />
                                    </li>
                                case (day === ''):
                                    return <li className='day ' key={di}>

                                        {day || "\u00a0"}

                                    </li>
                                case (day1.slice(-5, -3) === currentDay.slice(-5, -3) && day1.slice(0, 4) === currentDay.slice(0, 4) && day === Number(currentDay.slice(-2))):
                                    return <li className='day' key={di} >
                                        <div className="actualDay" style={{ background: '#fff5c7' }}>
                                            {day || "\u00a0"}

                                        </div>
                                        <Compo2 input="WORK" color="black" text="REGD" />

                                    </li>

                                default:
                                    return <li className='day' key={di}>

                                        <div className="actualDay" >
                                            {day || "\u00a0"}

                                        </div>
                                        <Compo2 input="WORK" color="black" text="REGD" />

                                    </li>
                            }
                        })()


                    })}
                </ul>)
            })
        }

    }
    componentDidUpdate(prevProps) {
        const { day1 } = this.state
        let elements = (<ul className="week2">
            {this.props.weeks[0].map((day, di) => {

                const { currentDay } = this.state;
                return (function () {
                    switch (true) {
                        case (di === 0 && day !== ''):
                            return <li className='day' key={di}>
                                <div className="actualDay">
                                    {day || "\u00a0"}
                                </div>
                                <Compo2 input="REST" color="white" text="REST" />
                            </li>
                        case (di === 6 && day !== ''):
                            return <li className='day' key={di} >
                                <div className="actualDay">
                                    {day || "\u00a0"}
                                </div>
                                <Compo2 input="REST" color="white" text="REST" />
                            </li>
                        case (day === ''):
                            return <li className='day ' key={di}>

                                {day || "\u00a0"}

                            </li>
                        case (day1.slice(-5, -3) === currentDay.slice(-5, -3) && day1.slice(0, 4) === currentDay.slice(0, 4) && day === Number(currentDay.slice(-2))):
                            return <li className='day' key={di} >
                                <div className="actualDay" style={{ background: '#fff5c7' }}>
                                    {day || "\u00a0"}

                                </div>
                                <Compo2 input="WORK" color="black" text="REGD" />

                            </li>

                        default:
                            return <li className='day' key={di}>

                                <div className="actualDay" >
                                    {day || "\u00a0"}

                                </div>
                                <Compo2 input="WORK" color="black" text="REGD" />

                            </li>
                    }
                })()




            })}
        </ul>)
        if (this.props.weeks !== prevProps.weeks) {
            this.setState({
                weeks: this.props.weeks,
                pageCount: Math.ceil(this.props.weeks.length / 1),
                elements: elements
            });

        }
    }

    setElementsForCurrentPage() {

        const { currentDay, day1 } = this.state;
        let elements = (<ul className="week2">
            {this.state.data[this.state.offset].map((day, di) => {
                return (function () {
                    switch (true) {
                        case (di === 0 && day !== ''):
                            return <li className='day' key={di}>
                                <div className="actualDay">
                                    {day || "\u00a0"}
                                </div>
                                <Compo2 input="REST" color="white" text="REST" />
                            </li>
                        case (di === 6 && day !== ''):
                            return <li className='day' key={di} >
                                <div className="actualDay">
                                    {day || "\u00a0"}
                                </div>
                                <Compo2 input="REST" color="white" text="REST" />
                            </li>
                        case (day === ''):
                            return <li className='day ' key={di}>

                                {day || "\u00a0"}

                            </li>
                        case (day1.slice(-5, -3) === currentDay.slice(-5, -3) && day1.slice(0, 4) === currentDay.slice(0, 4) && day === Number(currentDay.slice(-2))):
                            return <li className='day' key={di} >
                                <div className="actualDay" style={{ background: '#fff5c7' }}>
                                    {day || "\u00a0"}

                                </div>
                                <Compo2 input="WORK" color="black" text="REGD" />

                            </li>

                        default:
                            return <li className='day' key={di}>

                                <div className="actualDay" >
                                    {day || "\u00a0"}

                                </div>
                                <Compo2 input="WORK" color="black" text="REGD" />

                            </li>
                    }
                })()




            })}
        </ul>)
        this.setState({ elements: elements });
    }

    handlePageClick = (data) => {
        const selectedPage = data.selected;
        const offset = selectedPage * this.state.perPage;
        this.setState({ currentPage: selectedPage, offset: offset }, () => {
            this.setElementsForCurrentPage();
        });
    }
    render() {
        let paginationElement;
        if (this.state.pageCount > 1) {
            paginationElement = (
                <ReactPaginate
                    previousLabel="prev"
                    nextLabel="next"
                    breakLabel={<span className="gap">...</span>}
                    pageCount={this.state.pageCount}
                    onPageChange={this.handlePageClick}
                    forcePage={this.state.currentPage}
                    containerClassName={"pagination"}
                    previousLinkClassName={"previous_page"}
                    nextLinkClassName={"next_page"}
                    disabledClassName={"disabled"}
                    activeClassName={"active"}
                />
            );
        }
        return (
            <React.Fragment>
                {this.state.elements}
                {paginationElement}

            </React.Fragment>
        );
    }
}






