import React, { Component } from 'react';
import moment from 'moment';
import MomentCalendarFactory from 'moment-calendar-2/src/api';
import WeekComponent from './weekComponent'
import WholeMonth from './WholeMonth'
import WeekDisplay from './WeekDisplay'

class Calendar extends Component {

    constructor(props) {
        super(props)

        window.moment = moment;

        this.state = {
            date: this.props.date,
            day: 'day',
            isToggleOn: true

        }

        this.calendar = MomentCalendarFactory.getInstance();
        this.toggleButton = this.toggleButton.bind(this);
    }

    componentWillReceiveProps(newProps) {
        this.setState({
            date: newProps.date,
            day: 'day'
        });
     
    }
    toggleButton() {
        this.setState({isToggleOn: !this.state.isToggleOn})

    }

    render() {
        this.calendar.setCurrentDate(this.state.date);
        const { date } = this.state;
        const weeks = this.calendar.getWeeksTable(true).slice(1);
        const { isToggleOn } = this.state;
        return <div classstatus="calendar" className="calendarContainer">
            <button style={{padding: '0', margin:'0'}} onClick={this.toggleButton}>  {this.state.isToggleOn ? 'Month' : 'Week'} </button>
            <WeekComponent />
            {(function () {
                switch (true) {
                    case ( isToggleOn ):
                        return <WholeMonth weeks={weeks} day={date} />
                    default:
                        return <WeekDisplay weeks={weeks} day={date.slice(-2)} />;
                }
            })()}
  
        </div>
    }
}

export default Calendar;


