import React, { Component } from 'react';

export class WorkSchedule extends Component {


    constructor(props) {
        super(props);
        this.state = {};


    }



    render() {
        return (
            <ul id="1" className="shiftThirdColumnList" style={{ padding: '4%', paddingBottom: 'none' }}>

                <li style={{ display: 'flex', height: '20%' }}>
                    <span style={{ width: '30%', border: 'none', alignItems: 'flex-start' }} className="shiftThirdColumnElements">Control No.: </span>
                    <span style={{ width: '70%' }} className="shiftThirdColumnElements">WS19080201 </span>
                </li>
                <li style={{ display: 'flex', height: '20%', marginTop: '2%' }}>
                    <span style={{ width: '15%', border: 'none', }} className="shiftThirdColumnElements">Start:</span>
                    <span style={{ width: '35%', textAlign: 'center', border: '1px solid #e6e6e6' }} className="shiftThirdColumnElements">09/01/2019 </span>
                    <span style={{ width: '15%', border: 'none' }} className="shiftThirdColumnElements">End:</span>
                    <span style={{ width: '35%', textAlign: 'center', border: '1px solid #e6e6e6' }} className="shiftThirdColumnElements">10/5/2019 </span>
                </li>

                <li style={{ display: 'flex', height: '12%', marginTop: '2%', marginLeft: '2%' }}>
                    Description
                                </li>
                <li style={{ display: 'flex', height: '30%' }}>
                    <span style={{ width: '100%', justifyContent: 'left', alignItems: 'flex-start', padding: '2%' }} className="shiftThirdColumnElements">
                        Company Work Schedule (September)
                                    </span>
                </li>
            </ul>
        )
    }
}


