import React from 'react';


export default class WeekComponent extends React.Component {
 
    

    render() {
        return (
            <ul className="weekName">
                <li className='week1 sunday'>  </li>
                 <li className='week1 monday'></li>
                 <li className='week1 tuesday'>  </li>
                 <li className='week1 wednesday'>  </li>
                 <li className='week1 thursday'>   </li>
                 <li className='week1 friday'>   </li>
                 <li className='week1 saturday'>   </li>
            </ul>
        );
    }
}


