import React, { Component } from 'react';
import { Row, Col, Table } from 'reactstrap';
import BankDeposit from './BankDeposit';
import ThirdParyForm from './ThirdParyForm';

export class Invoice extends Component {
    
    handlePaymentMethodChange() {

        return (this.props.bankDeposit?<BankDeposit/>:<ThirdParyForm/>)
    }
    render() {
        var date = new Date();
        return (
            <div id="invoice-component-content">
                <div id ="invoice-header"> <h3>Invoice<p>
                    Date {date.getMonth()}{'/'} {date.getDate()} {'/'}
                    {date.getFullYear()} </p></h3>
                    
                </div>
                <Row>
                    <Col lg="4">
                        <p className="deliver-from-to" >From:</p>
                        <p>N-PAX PHILIPPINES INC.<br />
                            3rd Flr., Engy Tech Bldg.,<br />
                            Hernan Cortes St.,Banilad,<br />
                            Mandaue City,Cebu Philippines 6014<br />
                            Phone: (032) 422-8086<br />
                        Email: sales@n-pax.com</p>
                    </Col>
                    <Col lg="4">
                            <p className="deliver-from-to">To:</p>
                            <p>MYCOMPANY<br />
                            Unit 1, 3rd Flr.,Gauden Bldg.<br />
                                Basak,<br />
                                Lapu-Lapu City,Cebu, Philippines 6014<br />
                                Phone: (032) 422-1234<br />
                            Email: buy@mycompany.com</p>
                    </Col>
                    <Col lg="4">
                        <div style={{ textAlign: 'left' }}>
                           <p> Invoice No.:1234<br />
                            Order No.:<br />
                            Payment Due:
                            Account Name
                            MYCOMPANY</p>
                        </div>
                    </Col>
                </Row>
                <Row>
                    <Table bordered>
                        <thead className= "font-color">
                            <tr>
                                <th>
                                    QTY
                                </th>
                                <th>
                                    ITEM
                                </th>
                                <th>
                                    DESCRIPTION
                                </th>
                                <th>
                                    SERIAL NO.
                                </th>
                                <th>
                                    UNIT PRICE
                                </th>
                                <th>
                                    SUBTOTAL
                                </th>
                            </tr>
                        </thead>
                        <tbody>
                            <tr>
                                <td>2</td>
                                <td>PAXYPAD DEVICE</td>
                                <td>Android Table Timekeeping Device</td>
                                <td>012356</td>
                                <td>PHP 12 000</td>
                                <td>PHP 24 000</td>
                            </tr>
                        </tbody>
                    </Table>
                </Row>
                <Row id = "summary-details">
                    <Col id="sumary-col" lg={{ size: 4, offset: 8 }}>
                        <Table bordered>
                            <tbody>
                                <tr>
                                    <td >Subtotal:</td>
                                    <td>PHP 12 000</td>
                                </tr>
                                <tr>
                                    <td>Tax (12% VAT)</td>
                                    <td>PHP 1 440</td>
                                </tr>
                                <tr>
                                    <td>Shipping:</td>
                                    <td>PHP 290</td>
                                </tr>
                                <tr>
                                    <td>Total:</td>
                                    <td>PHP 13 370</td>
                                </tr>
                            </tbody>
                        </Table>
                    </Col>
                </Row>
                {
                    this.handlePaymentMethodChange()
                }
            </div>
        );
    }
}


