import React from 'react';
import Compo2 from './compo2'

export default class WholeMonth extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            timeStatus: props.timeStatus,
            status: props.status,
            time: props.time,
            border: props.border,
            day1: props.day,
            key: props.key,
            input: props.input,
            weeks: props.weeks,
            currentDay: this.props.day
        };

    }
    componentDidUpdate(prevProps) {
        if (this.props.weeks !== prevProps.weeks) {
            this.setState({ weeks: this.props.weeks, day1: this.props.day });
        }
        console.log(this.state.currentDay);
        console.log(this.state.day1.slice(0, 4));
    }
    componentDidMount() {
        if (this.props.day[0] === '0') {
            this.setState({ currentDay: this.props.day.slice(1) });
        }


    }
    render() {
        const { currentDay, day1 } = this.state;
        return (
            <React.Fragment>
                {this.state.weeks.map((days, i) => {
                    return <ul className="week" key={i}>
                        {days.map((day, di) => {
                            return (function () {
                                switch (true) {
                                    case (di === 0 && day !== ''):
                                        return <li className='day' key={di}>
                                            <div className="actualDay">
                                                {day || "\u00a0"}
                                            </div>
                                            <Compo2 input="REST" color="white" text="REST" />
                                        </li>
                                    case (di === 6 && day !== ''):
                                        return <li className='day' key={di} >
                                            <div className="actualDay">
                                                {day || "\u00a0"}
                                            </div>
                                            <Compo2 input="REST" color="white" text="REST" />
                                        </li>
                                    case (day === ''):
                                        return <li className='day ' key={di}>

                                            {day || "\u00a0"}

                                        </li>
                                    case (day1.slice(-5, -3) === currentDay.slice(-5, -3) && day1.slice(0, 4) === currentDay.slice(0, 4) && day === Number(currentDay.slice(-2))):
                                        return <li className='day' key={di} >
                                            <div className="actualDay" style={{ background: '#fff5c7' }}>
                                                {day || "\u00a0"}

                                            </div>
                                            <Compo2 input="WORK" color="black" text="REGD" />

                                        </li>

                                    default:
                                        return <li className='day' key={di}>

                                            <div className="actualDay" >
                                                {day || "\u00a0"}

                                            </div>
                                            <Compo2 input="WORK" color="black" text="REGD" />

                                        </li>
                                }
                            })()



                        })}
                    </ul>
                })}
            </React.Fragment>
        );
    }
}






