import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

export class TimeRecordCaptureSettings extends Component {
    
    constructor(props) {

        super(props);
        this.state = {
            nfc: false,
            fingerprint: false
        };
    }

    handleNFCChange = () => {
        this.setState({
            nfc: !this.state.nfc
        })
    }
    handleFingerprintChange = () => {

        this.setState({
            fingerprint: !this.state.fingerprint
        })
    }
    render() {
        var arrowClass = this.props.isShown ? "fas fa-arrow-up" : "fas fa-arrow-down"
        return (
            < div id = "timerec-settings">
                <div id= "timerec-setting-head"
                    onClick={this.props.onClick} >
                    <Row>
                        <Col lg={{ size: 8, offset: 2 }}>
                            Device Capture Settings
                        </Col>
                        <Col lg={{ size: 2 }}>
                            <span className={arrowClass + ' align-right'}
                            />
                        </Col>
                    </Row>
                </div>
                <div>
                    {
                        this.props.isShown ? <Setting
                            onNFCClick={this.handleNFCChange.bind(this)}
                            onFingerClick={this.handleFingerprintChange.bind(this)} /> : null
                    }
                </div>


            </div>
        );
    }
}
export class Setting extends Component {

    constructor(props) {
        super(props)
        this.state = {
            geolocation: false

        };

    }

    render() {

        return (
            <div id= "timerec-setting-body">
                <Row>
                    <Col lg="8" className ="align-left">
                        <span className="font-weight-bold">Biometric (Fingerprint)</span>
                    </Col>
                    <Col lg="4" className= "toggle-switch">
                        <label className="switch" >
                            <input type="checkbox" className="checked" />
                            <span className="slider round" onClick={this.props.onFingerClick} ></span>
                        </label>
                    </Col>
                </Row>
                <Row>
                    <Col lg="6" className="align-left">
                        <span className="font-weight-bold">NFC Card</span>
                    </Col>
                    <Col lg="6" className="toggle-switch">
                        <label className="switch" >
                            <input type="checkbox"  />
                            <span className="slider round" onClick={this.props.onNFCClick} ></span>
                        </label>
                    </Col>
                </Row>
            </div >);
    }
} 


