import React, { Component } from 'react';

export class CustomControl extends Component {
    
    render() {
        var bankdepClass = this.props.bankDeposit ? "payment-selected" : "";
        var othersDepClass = this.props.bankDeposit ? "" : "payment-selected";
        
        return (
            <div id = "custom-control">
                <div id ="bank-deposit" className={'payment ' + bankdepClass}
                    onClick={this.props.onBankClick}>Bank Deposit
                </div>
                <div className={'payment ' + othersDepClass}
                    onClick={this.props.onOtherClick}>Other Payment Methods
                </div>
            </div>
        );
    }
}
export default CustomControl;


