import React, { Component } from 'react';
import {
    Row, Col, FormGroup, CardBody, CardTitle, Card, Input,
    CardText, Label, Form, Table, InputGroupAddon, InputGroup, InputGroupText
} from 'reactstrap';
import image from '../../img/Device.png';
import CustomControl from './CustomControl';
import DropdownButton from './DropdownButton';
import { Invoice } from './Invoice';
import { LocationSetting } from "./LocationSetting";
import { TimeRecordCaptureSettings } from "./TimeRecordCaptureSettings";

export function IntroContent() {
    
        return (
            <div id="content1">
                <h2>Introduction</h2>
                <Row >
                    <Col lg="6" md="6" sm="12" xs="12">
                        <Card id= "card-content">
                            <CardBody>
                                <CardTitle>What is Paxyroll?
                                    <i className="fas fa-question-circle fa-2x"
                                    ></i></CardTitle>
                                <CardText>Paxyroll is an online Human Resource Companion payroll
                                    application. This is designed to serve small to medium
                                    business to simplify timekeeping and payroll processing
                                    and improve record accessibility</CardText>
                            </CardBody>
                        </Card>
                        <Card id="card-content">
                            <CardBody>
                                <CardTitle>How can Paxyroll help?
                                    <i className="fas fa-question-circle fa-2x"></i></CardTitle>
                                <CardText>Manage all your HR administrative actions from a centralized
                                    location. Manage timekeeping and process payroll in a centralized
                                    and secured cloud infrastracture. Manage records and generate
                                    reports anywhere.</CardText>
                            </CardBody>
                        </Card>
                        <Card id="card-content">
                            <CardBody>
                                <CardTitle>Track Time and Attendace easily.
                                    <i className="fas fa-calendar-check fa-2x"></i></CardTitle>
                                <CardText>Eliminate errors due to manual entries, consolidate
                                    attendace record from multiple devices, manage leaves, schedule
                                    shifts and do payroll the easy way.</CardText>
                            </CardBody>
                        </Card>
                    </Col>
                    <Col lg="6" md="6" sm="12" xs="12">
                        <Card id="card-content">
                            <CardBody>
                                <CardTitle>How does Paxyroll Works?
                                    <i className="fas fa-question-circle fa-2x"></i></CardTitle>
                                <CardText>Timekeeping is done through Paxypad device
                                    which is connected to the internet  and then carptured
                                    time records are gathered by Paxyroll operation
                                    services that runs around the clock in the
                                    secured cloud enviroment.

                                    View information and do payroll processing through Paxyroll
                                    Operations Web App which is available in a browser
                                    that works in mobile or in desktop.</CardText>
                            </CardBody>
                        </Card>
                        <Card id="card-content">
                            <CardBody>
                                <CardTitle>Paxypad Timekeeping device
                                    <i className="fas fa-tablet-alt fa-2x"></i></CardTitle>
                                <CardText>This device is multi-functional handheld fingerprint
                                    android terminal which integrates fingerprint and
                                    NFC card reader into single android device. This is the only
                                    timekeeping device compatible with paxyroll. </CardText>
                            </CardBody>
                        </Card>
                    </Col>
                </Row>
                
            </div>
        );
}
export function OrderDevice () {
   
        return (
            <div id="order-device-id">
                <h2>Order Device</h2>
                <Row id = "order-row-1">
                    <Col id ="order-col-1" lg="6" md="6" sm ="12" xs = "12"> 
                        <img src={image} alt = "error" />
                    </Col>
                    <Col lg="6" md="6" sm="12" xs="12">
                        <h3>Paxypad Timekeeping Device</h3><br/>
                        <h4>Specifications:</h4>
                        <ul id ="tofer">
                            <li >Wifi Connection</li>
                            <li>Dual SIM (SIM not included)</li>
                            <li>Installed with Employee Registration and Timekeeping app</li>
                            <li>Built-in camera</li>
                            <li>Built-in NFC Card reader</li>
                            <li>Built-in Fingerprint scanner</li>
                            <li>Support GPS Location</li>
                        </ul>
                    </Col>
                </Row>
                <Row>
                    <Col lg = "2" md="2" sm = "1" >

                    </Col >
                    <Col lg="8" md="8" sm="10" >
                        <div >
                            <Row>
                                <Col id="paxy-pad-price" lg="6">
                                    <div id="unit-price-label">
                                        <Row>
                                            <Col lg = "6">
                                                <div>
                                                    <p>Unit Price</p>
                                                </div>
                                            </Col>
                                            <Col lg="6">
                                                <h2 >PHP 12000</h2>
                                            </Col>
                                        </Row>
                                    </div>
                                    <div id = "curr-value">
                                        <Row>
                                            <Col lg="5"> 
                                                Currency
                                            </Col>
                                            <Col lg="7">
                                                <div className="btn-group">
                                                    <DropdownButton/>
                                                </div>
                                            </Col>
                                        </Row>
                                        
                                    </div>
                                </Col>
                                <Col id= "quantity-col-id"  lg="6">
                                    <div id = "main-div" >
                                        <div id = "offset1">
                                        </div>
                                        <div id = "main-content" >
                                            <Label for="exampleEmail">Order Quantity</Label> {' '}
                                            <Input
                                                type="number" name="qty" id="idqty" placeholder="0" />{' '}
                                            <i id = "additem" className="fas fa-plus-circle fa-2x"></i>{' '}
                                            <i id = "removeitem" className="fas fa-minus-circle fa-2x"></i>
                                        </div>
                                        <div id="offset1">
                                        </div>
                                    </div>
                                </Col>
                            </Row>
                        </div>
                    </Col>
                    <Col lg="2" md="2" sm="1" >

                    </Col>
                </Row>
            </div>
        );
    
}
export function DeliveryInfoContent() {
        return (
            <div id="delivery-info">
                <h2> Delivery Information</h2>
                <hr />
                <h3> Deliver To</h3>
                <Form>
                    <Row>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="company">Company</Label>
                                <Input type="text" name="company" id="company" placeholder="Company Name" />
                            </FormGroup>
                        </Col>
                      
                    </Row>
                   
                    <Row>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="country">Country</Label>
                                <Input type="text" name="country" id="country" />
                            </FormGroup>
                        </Col>
                        <Col lg="4" md="4" sm="4" sm="12">
                            <FormGroup>
                                <Label className="font-color-label" for="province">Province</Label>
                                <Input type="text" name="province" id="province" />
                            </FormGroup>
                        </Col>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="city">City/Municipality</Label>
                                <Input type="text" name="city" id="city"     />
                            </FormGroup>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg="8" md="8" sm="8" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="unitAddress">Unit,Floor No., Building, Street</Label>
                                <Input type="text" name="unitAddress" id="unitAddress"/>
                            </FormGroup>
                        </Col>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="barangay">Barangay/District</Label>
                                <Input type="text" name="barangay" id="barangay" />
                            </FormGroup>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg="2" md="2" sm="2" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="zipcode">Zip Code</Label>
                                <Input type="text" name="zipcode" id="zipcode" />
                            </FormGroup>
                        </Col>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="telno">Tels. No.(s):</Label>
                                <Input type="text" name="telno" id="telno" />
                            </FormGroup>
                        </Col>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="landmark">Nearest Landmark (Optional)</Label>
                                <Input type="text" name="landmark" id="landmark" />
                            </FormGroup>
                        </Col> 
                    </Row>
                    <hr/>
                    <h3> Deliver To</h3>
                    <Row>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="name">Name</Label>
                                <Input type="text" name="name" id="name" />
                            </FormGroup>
                        </Col>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="contactnuber">Contact No.(s):</Label>
                                <Input type="text" name="contactnuber" id="contactnuber" />
                            </FormGroup>
                        </Col>
                        <Col lg="4" md="4" sm="4" xs="12">
                            <FormGroup>
                                <Label className="font-color-label" for="email">Email (Optional)</Label>
                                <Input type="text" name="email" id="email" />
                            </FormGroup>
                        </Col>
                    </Row>
                    <Card id="note-card" outline color="warning">
                        <CardBody id="card-body">
                            <CardTitle id= "card-title" >Note:</CardTitle>
                            <CardText id="card-text">Please provide valid contact number and or email address to enable us
                                to contact regarding purhase or delivery. We might reach out to the delivery
                                Contact Person if needed.</CardText>
                        </CardBody>
                    </Card>
                </Form>
            </div>
        );
}
export class Payment extends Component {

    constructor(props) {
        super(props)
        this.state = {

            bankDeposit:true
        }
    }

    handleBankDeposit=() => {
        this.setState({
            bankDeposit: true
        });
    }
    handleOthers = () => {
        this.setState({
            bankDeposit : false
        });
    }

    render() {
        
        return (
            <div id="payment-invoice" >
                <div>
                    <h2>Invoice and Payment</h2>
                </div>
                <div id = "header">
                    <Label>Payment Method: </Label>{' '}
                    <CustomControl
                        onBankClick={this.handleBankDeposit.bind(this)}
                        onOtherClick={this.handleOthers.bind(this)}
                        bankDeposit={this.state.bankDeposit} />
                </div>
                <hr />
                <div>
                    <Invoice bankDeposit = {this.state.bankDeposit}/>
                </div>
                
            </div>
        );
    }
}
export class ManageDevice extends Component {

    constructor(props) {
        super(props);
        this.state = {
            hidePin: true,
            locationSettingCollapse: false,
            captureSettingCol: false
        };
    }

    onHandleHideClick = () => {

    }
    handleCollapse = () => {
        this.setState({ locationSettingCollapse: !this.state.locationSettingCollapse });
    }
    handleCaptureCollapse = () => {
        this.setState({ captureSettingCol: !this.state.captureSettingCol });
    }
    render() {

        return (
            <div id="manage-device-content">
                <h2>Manage</h2>
                <Card id = "note-card" outline color="warning">
                    <CardBody id = "card-body">
                        <CardTitle id="card-title" >Note:</CardTitle>
                        <CardText id="card-text">
                        Only newly ordered devices will appear here. Information such as Device
                        Serial No., Device Location, Last Communication will only appear after device is
                        registered. You can change assigned Device and PIN Code in this screen.</CardText>
                    </CardBody>
                </Card>
                <Row id = "paxypad-info-tbl">
                    <Col lg="8">
                        <Table bordered>
                            <thead>
                                <tr >
                                    <th>
                                        Device  Serial No.
                                </th>
                                    <th>
                                        Device Name
                                </th>
                                    <th>
                                        Device Location
                                </th>
                                    <th>
                                        Last Communication
                                </th>
                                    <th>
                                        Status
                                </th>

                                </tr>
                            </thead>
                            <tbody style={{ textAlign: 'center' }}>
                                <tr>
                                    <td>-</td>
                                    <td>Paxypad 1</td>
                                    <td>-</td>
                                    <td>-</td>
                                    <td>In-Process</td>
                                </tr>
                            </tbody>
                        </Table>
                    </Col>
                    <Col id = "paxypad-details" lg="4" >
                        <div id = "detail-head"> 
                            <i  className="fas fa-edit"/>
                        </div>
                        <Row>
                            <Col lg="6">
                                <FormGroup>
                                    <Label for="serial">Serial No:</Label>
                                    <Input type="text" name="serial" id="serial" />
                                </FormGroup>
                            </Col>
                            <Col lg="6">
                                <FormGroup>
                                    <Label  for="company">Status</Label>
                                    <Input type="text" name="status" id="status" />
                                </FormGroup>
                            </Col>
                        </Row>
                        <Row>
                            <Col lg="9">
                                <FormGroup>
                                    <Label for="devicename">Device Name:</Label>
                                    <Input type="text" name="devicename" id="devicename"/>
                                </FormGroup>
                            </Col>
                            <Col lg="3">
                                
                            </Col>
                        </Row>
                        <Row>
                            <Col lg="9" style={{ display: 'inline' }}>
                                <Label >Pin Code:</Label>
                                <InputGroup>
                                    <Input />
                                    <InputGroupAddon addonType="append">
                                        <InputGroupText><i className="fas fa-eye-slash"
                                            onClick={this.onHandleHideClick.bind(this)}
                                            style={{cursor:'pointer'}}></i></InputGroupText>
                                    </InputGroupAddon>
                                </InputGroup>
                            </Col>
                            <Col lg="3" style={{paddingTop:'40px',textAlign:'left',marginLeft:'0px',paddingLeft:'0px'}}>
                                
                            </Col>
                        </Row>
                        <div id ="pin-warning">
                           It is recommended that you change you pin code.
                        </div>
                        <Row>
                            <Col lg="9">
                                <FormGroup>
                                    <Label for="company">Company</Label>
                                    <Input type="text" name="company" id="company" placeholder="Company Name" />
                                </FormGroup>
                            </Col>
                            <Col lg="3">

                            </Col>
                        </Row>
                        <Row>
                            <Col lg="9">
                                <FormGroup>
                                    <Label for="company">Last Communication</Label>
                                    <Input type="text" name="lastcom" id="lastcom"  />
                                </FormGroup>
                            </Col>
                            <Col lg="3">

                            </Col>
                        </Row>
                        <Row>
                            <LocationSetting
                                isShown={this.state.locationSettingCollapse}
                                onClick={this.handleCollapse.bind(this)} />
                        </Row>
                        <Row>
                            <TimeRecordCaptureSettings
                                isShown={this.state.captureSettingCol}
                                onClick={this.handleCaptureCollapse.bind(this)} />
                        </Row>
                    </Col>
                </Row>
            </div>
        );
    }
}
export class GettingFamiliar extends Component {

    render() {

        return (
            <div id="get-familiar-content">
                <h2>Getting Familiar</h2>
                <p>While waiting for the processing and delivery of the device(s), you can proceed
                    with getting familiar with the system.</p>
                <p>Which one would you like to do next?</p>
                <div class="custom-control custom-radio">
                    <input type="radio" id="customRadio1" name="customRadio" class="custom-control-input" />
                    <label class="custom-control-label" for="customRadio1">See Tutorial</label>
                </div>
                <div class="custom-control custom-radio">
                    <input type="radio" id="customRadio2" name="customRadio" class="custom-control-input" />
                    <label class="custom-control-label" for="customRadio2">Add delegate admin</label>
                </div>
                <div class="custom-control custom-radio">
                    <input type="radio" id="customRadio3" name="customRadio" class="custom-control-input" />
                    <label class="custom-control-label" for="customRadio3">Explore workspace</label>
                </div>
            </div>
        );
    }
}


